// Input.tsx
import React from 'react';

import clsx from 'clsx';
import { focusRing } from '../../../tailwind/styles/focus';
import { InputProps, InputScope } from './input.interface';
import { borderRadius } from '../../../tailwind/styles/border-radius';
import { FunctionalIcon } from '../icon/Icon';

export const Input: React.FC<InputProps> = ({
  id,
  type,
  value,
  error,
  label,
  disabled = false,
  icon,
  readOnly = false,
  hasInfoIcon = false,
  message,
  onIconToggle,
  onIconInfoClick,
  onChange,
  ...props
}) => {
  const searchCancel = clsx(
    'search-cancel:appearance-none',
    'search-cancel:w-5',
    'search-cancel:h-5',
    'search-cancel:border-[15px]',
    'search-cancel:border-solid',
    'search-cancel:border-transparent',
    'search-cancel:cursor-pointer',
    'search-cancel:bg-no-repeat',
    'search-cancel:mr-[-16px]',
    'search-cancel:bg-[url(/src/assets/search-cancel.svg)]',
  );

  const calendarPicker = clsx(
    'calendar-picker:appearance-none',
    'calendar-picker:w-5',
    'calendar-picker:h-5',
    'calendar-picker:p-0',
    'calendar-picker:border-x-[16px]',
    'calendar-picker:border-solid',
    'calendar-picker:border-transparent',
    'calendar-picker:cursor-pointer',
    'calendar-picker:bg-no-repeat',
    'calendar-picker:mr-[-16px]',
    'calendar-picker:bg-[url(/src/assets/calendar.svg)]',
  );

  return (
    <span
      className={clsx(
        InputScope,
        'zep-inline-flex',
        'zep-flex-col',
        'zep-flex-wrap',
        'zep-items-stretch',
        'zep-justify-start',
        'zep-relative',
        [disabled ? 'zep-opacity-zep-40 zep-cursor-not-allowed' : 'zep-cursor-pointer'],
      )}
      data-testid="zep-input"
    >
      {label && (
        <label htmlFor={id} className={clsx('zep-mb-0.5', 'zep-text-input-label-default')}>
          {label}
        </label>
      )}
      <div className={clsx('zep-inline-flex', 'zep-flex-row', 'zep-flex-nowrap', 'zep-items-center', 'zep-gap-0.5')}>
        <div className={clsx('zep-relative', 'zep-w-full')}>
          <input
            id={id}
            value={value}
            type={type}
            onChange={!disabled ? onChange : undefined}
            aria-describedby={`aria-describedby-${id}`}
            className={clsx(
              borderRadius,
              focusRing,
              'zep-border-solid', // todo to be replaced with zep-border-input-default
              'zep-border-1', // todo to be replaced with zep-border-input-default
              'zep-w-full',
              'zep-rounded-4', // todo replace with zep-rounded-input-default
              'zep-h-3', // todo use height token
              'zep-px-1',
              'zep-leading-[48px]',
              'zep-leading-[22px]',
              'focus-visible:zep-ring-offset-1',
              'zep-text-input-placeholder-default zep-placeholder-input-placeholder-default/70',
              'invalid:[&:not(:placeholder-shown):not(:focus)]:zep-border-error',
              'invalid:[&:not(:placeholder-shown):not(:focus)]:zep-border-2',
              { 'zep-border-inbox-default': !error }, // todo use it
              { 'zep-border-2 zep-border-error': error },
              {
                'zep-text-input-placeholder-readOnly zep-bg-input-placeholder-readonly zep-cursor-not-allowed':
                  readOnly, // does not work at the moment
              },
              { [calendarPicker]: type === 'date' },
              { [searchCancel]: type === 'search' },
            )}
            disabled={disabled}
            data-testid="zep-input-field"
            {...props}
          />
          {icon && (
            <FunctionalIcon
              onClick={onIconToggle}
              className={clsx('zep-absolute', 'zep-top-[0px]', 'zep-bottom-0', 'zep-right-1', 'zep-h-full', {
                'zep-cursor-pointer': onIconToggle,
              })}
              name={icon}
            />
          )}
        </div>
        {hasInfoIcon && <FunctionalIcon onClick={onIconInfoClick} name="Info" />}
      </div>
      {message && (
        <div
          className={clsx(
            'zep-inline-flex',
            'zep-flex-row',
            'zep-items-center',
            'zep-gap-0.25',
            'zep-text-error',
            'zep-mt-0.25',
          )}
        >
          <FunctionalIcon name="Warning" />
          <span id={`aria-describedby-${id}`}>{message}</span>
        </div>
      )}
    </span>
  );
};
